body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Light theme (default) */
body.light {
  --bg-color: #f5f5f5;
  --card-bg: white;
  --text-color: #333;
  --text-secondary: #666;
  --text-muted: #999;
  --border-color: #ddd;
  --shadow: rgba(0,0,0,0.1);
  --button-bg: #3b82f6;
  --button-hover: #2563eb;
  --header-bg: #f8f9fa;
  --success-color: #28a745;
  --error-color: #dc3545;
}

/* Dark theme */
body.dark {
  --bg-color: #1a1a1a;
  --card-bg: #2d2d2d;
  --text-color: #e0e0e0;
  --text-secondary: #ccc;
  --text-muted: #888;
  --border-color: #444;
  --shadow: rgba(0,0,0,0.3);
  --button-bg: #4f46e5;
  --button-hover: #4338ca;
  --header-bg: #333;
  --success-color: #40c057;
  --error-color: #fa5252;
}

.app-container {
  min-height: 100vh;
  background-color: var(--bg-color);
  color: var(--text-color);
  padding: 1rem;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.dashboard {
  max-width: 1200px;
  margin: 0 auto;
}

.header {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 1rem;
  color: var(--text-color);
}

.grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1rem;
  margin-bottom: 1rem;
}

.card {
  background-color: var(--card-bg);
  color: var(--text-color);
  padding: 1rem;
  border-radius: 0.5rem;
  box-shadow: 0 2px 4px var(--shadow);
  border: 1px solid var(--border-color);
  transition: background-color 0.3s ease, color 0.3s ease, border-color 0.3s ease;
}

.card h2 {
  font-size: 1.25rem;
  font-weight: bold;
  margin-bottom: 0.5rem;
  color: var(--text-color);
}

.upload-section {
  background-color: var(--card-bg);
  color: var(--text-color);
  padding: 1rem;
  border-radius: 0.5rem;
  box-shadow: 0 2px 4px var(--shadow);
  border: 1px solid var(--border-color);
  margin-bottom: 1rem;
  transition: background-color 0.3s ease, color 0.3s ease, border-color 0.3s ease;
}

.upload-section h2 {
  font-size: 1.25rem;
  font-weight: bold;
  margin-bottom: 0.5rem;
  color: var(--text-color);
}

.button {
  background-color: var(--button-bg);
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 0.25rem;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.button:hover {
  background-color: var(--button-hover);
}

.preroll-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 1rem;
}

.preroll-item {
  background-color: var(--card-bg);
  color: var(--text-color);
  border: 1px solid var(--border-color);
  padding: 0.5rem;
  border-radius: 0.25rem;
  transition: background-color 0.3s ease, color 0.3s ease, border-color 0.3s ease;
}

.preroll-item img {
  width: 100%;
  height: 5rem;
  object-fit: cover;
  border-radius: 0.25rem;
}


.tab-buttons {
  position: sticky;
  top: 0;
  z-index: 900;
  display: flex;
  margin-bottom: 1rem;
  border-bottom: 1px solid var(--border-color);
  background-color: var(--header-bg);
  border-radius: 0.5rem 0.5rem 0 0;
  overflow: hidden;
  box-shadow: 0 2px 4px var(--shadow);
}

.tab-button {
  padding: 0.75rem 1.5rem;
  border: none;
  background: transparent;
  color: var(--text-color);
  cursor: pointer;
  border-bottom: 2px solid transparent;
  transition: all 0.3s ease;
}

.tab-button.active {
  background-color: var(--button-bg);
  color: white;
  border-bottom-color: var(--button-bg);
}

.tab-button:hover:not(.active) {
  background-color: var(--card-bg);
}

/* ---------- Modal (nx-) ---------- */
.nx-modal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0,0,0,0.45);
  backdrop-filter: blur(2px);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  z-index: 1000;
}

@keyframes nx-fade-up {
  from { opacity: 0; transform: translateY(10px) scale(0.98); }
  to   { opacity: 1; transform: translateY(0) scale(1); }
}

.nx-modal {
  background: var(--card-bg);
  color: var(--text-color);
  border: 1px solid var(--border-color);
  border-radius: 12px;
  box-shadow: 0 12px 30px var(--shadow);
  width: 100%;
  max-width: 700px;
  max-height: 85vh;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  animation: nx-fade-up 0.18s ease-out;
}

.nx-modal-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 1.25rem;
  border-bottom: 1px solid var(--border-color);
  background: var(--header-bg);
}

.nx-modal-title {
  margin: 0;
  font-size: 1.25rem;
  color: var(--text-color);
}

.nx-modal-close {
  border: none;
  background: transparent;
  color: var(--text-color);
  font-size: 1.25rem;
  cursor: pointer;
  border-radius: 8px;
  width: 36px;
  height: 36px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.nx-modal-close:hover {
  background: var(--card-bg);
}

.nx-modal-body {
  padding: 1rem 1.25rem;
  overflow: auto;
}

/* ---------- Form layout ---------- */
.nx-form-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
}

.nx-field {
  display: flex;
  flex-direction: column;
  gap: 0.35rem;
}

.nx-span-2 {
  grid-column: span 2;
}

.nx-label {
  font-size: 0.9rem;
  color: var(--text-color);
  opacity: 0.9;
}

.nx-input,
.nx-select,
.nx-textarea {
  width: 100%;
  padding: 0.5rem 0.6rem;
  border: 1px solid var(--border-color);
  background: var(--card-bg);
  color: var(--text-color);
  border-radius: 0.4rem;
  box-shadow: inset 0 1px 2px var(--shadow);
}

.nx-input:focus,
.nx-select:focus,
.nx-textarea:focus {
  outline: none;
  border-color: var(--button-bg);
  box-shadow: 0 0 0 3px rgba(59,130,246,0.2);
}

/* Placeholder text styling */
.nx-input::placeholder,
.nx-select::placeholder,
.nx-textarea::placeholder {
  color: var(--text-muted);
  opacity: 1;
}

/* Dropdown options styling */
.nx-select option {
  background-color: var(--card-bg);
  color: var(--text-color);
}

.nx-textarea {
  resize: vertical;
  min-height: 90px;
}

/* ---------- Actions ---------- */
.nx-actions {
  margin-top: 1rem;
  display: flex;
  gap: 0.5rem;
  justify-content: flex-end;
}

.button-secondary {
  background-color: #6c757d;
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 0.25rem;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.button-secondary:hover {
  background-color: #5a6268;
}

/* ---------- Responsive ---------- */
@media (max-width: 720px) {
  .nx-form-grid { grid-template-columns: 1fr; }
  .nx-span-2 { grid-column: span 1; }
  .nx-modal { max-height: 90vh; }
}

/* ---------- CategoryPicker (nx-catpicker) ---------- */
.nx-catpicker {
  position: relative;
}

.nx-catpicker-input {
  min-height: 40px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 6px;
  padding: 6px 40px 6px 8px;
  border: 1px solid var(--border-color);
  background: var(--card-bg);
  color: var(--text-color);
  border-radius: 8px;
  cursor: text;
  box-shadow: inset 0 1px 2px var(--shadow);
}

.nx-catpicker-input.open {
  border-color: var(--button-bg);
  box-shadow: 0 0 0 3px rgba(59,130,246,0.2);
}

.nx-catpicker-hidden-input {
  border: none;
  outline: none;
  background: transparent;
  color: var(--text-color);
  padding: 4px;
  min-width: 120px;
  flex: 1;
}

.nx-catpicker-clear {
  position: absolute;
  right: 8px;
  top: 8px;
  height: 24px;
  padding: 0 8px;
  background: var(--button-bg);
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 0.8rem;
}

.nx-chip {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  padding: 2px 8px;
  border-radius: 9999px;
  background: var(--header-bg);
  border: 1px solid var(--border-color);
  color: var(--text-color);
  font-size: 0.85rem;
}

.nx-chip.primary {
  background: #fffbeb;
  border-color: #fbbf24;
  color: #92400e;
}

body.dark .nx-chip.primary {
  background: #3a2d00;
  border-color: #fbbf24;
  color: #ffdd57;
}

.nx-chip.ghost {
  opacity: 0.7;
  font-style: italic;
}

.nx-chip-x,
.nx-chip-star {
  border: none;
  background: transparent;
  color: inherit;
  cursor: pointer;
  padding: 0;
  line-height: 1;
}

.nx-chip-x:hover { color: #dc3545; }
.nx-chip-star:hover { color: #f59e0b; }

.nx-catpicker-dropdown {
  position: absolute;
  left: 0;
  right: 0;
  top: calc(100% + 4px);
  background: var(--card-bg);
  color: var(--text-color);
  border: 1px solid var(--border-color);
  border-radius: 8px;
  box-shadow: 0 8px 20px var(--shadow);
  z-index: 100;
  overflow: hidden;
}

.nx-catpicker-search {
  padding: 0.5rem;
  border-bottom: 1px solid var(--border-color);
}

.nx-catpicker-search input {
  width: 100%;
  padding: 0.5rem;
  border: 1px solid var(--border-color);
  background: var(--card-bg);
  color: var(--text-color);
  border-radius: 6px;
}

.nx-catpicker-list {
  max-height: 260px;
  overflow: auto;
}

.nx-catpicker-item {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.45rem 0.6rem;
  border-bottom: 1px solid var(--border-color);
}

.nx-catpicker-item:last-child {
  border-bottom: none;
}

.nx-catpicker-item.selected {
  background: rgba(59,130,246,0.08);
}

.nx-catpicker-name {
  flex: 1;
}

.nx-catpicker-star {
  border: none;
  background: transparent;
  cursor: pointer;
  font-size: 1rem;
  color: var(--text-color);
  opacity: 0.6;
}

.nx-catpicker-star.active {
  color: #f59e0b;
}

.nx-catpicker-empty {
  padding: 0.75rem;
  font-size: 0.9rem;
  color: var(--text-color);
  opacity: 0.7;
}

@media (max-width: 720px) {
  .nx-catpicker-input { padding-right: 34px; }
  .nx-catpicker-clear { top: 6px; }
}

/* ---------- Plex page enhancements ---------- */
.nx-plex-card h2 { margin-top: 0; }

.nx-plex-steps {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  align-items: center;
  margin: 0.25rem 0 1rem;
}

.nx-step {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  padding: 2px 10px;
  background: var(--header-bg);
  border: 1px solid var(--border-color);
  border-radius: 9999px;
  font-size: 0.85rem;
}

.nx-badge {
  width: 18px;
  height: 18px;
  border-radius: 9999px;
  background: var(--button-bg);
  color: #fff;
  display: inline-grid;
  place-items: center;
  font-size: 0.75rem;
  font-weight: 700;
}

/* Method sections */
.nx-plex-method {
  border-left: 4px solid var(--button-bg);
  background: var(--card-bg);
}

/* Info note */
.nx-plex-note {
  background: var(--header-bg);
}

/* Button variants */
.button-success { background-color: #28a745 !important; }
.button-danger  { background-color: #dc3545 !important; }
.button-warn    { background-color: #f59e0b !important; }
.button-outline {
  background: transparent !important;
  color: var(--text-color) !important;
  border: 1px solid var(--border-color) !important;
}

/* Status chip */
.nx-chip.nx-status {
  padding: 0.1rem 0.5rem;
  border-radius: 9999px;
  border: 1px solid var(--border-color);
  background: var(--header-bg);
  font-size: 0.85rem;
}

.nx-chip.nx-status.ok {
  background: rgba(40,167,69,0.15);
  border-color: rgba(40,167,69,0.45);
  color: #1e7e34;
}

.nx-chip.nx-status.bad {
  background: rgba(220,53,69,0.15);
  border-color: rgba(220,53,69,0.45);
  color: #842029;
}

/* Collapsible help */
details.nx-plex-help { margin-top: 0.25rem; }
details.nx-plex-help summary { cursor: pointer; color: var(--button-bg); }
details.nx-plex-help[open] {
  background: var(--card-bg);
  border: 1px dashed var(--border-color);
  border-radius: 8px;
  padding: 0.5rem 0.75rem;
}

/* Sticky top navigation refinement for better UX on scroll */
.tabbar-right { display: flex; align-items: center; }

/* Preroll card header and actions: prevent overflow on long titles */
.preroll-header { display: flex; justify-content: space-between; align-items: flex-start; gap: 0.5rem; min-width: 0; }
.preroll-title { white-space: nowrap; overflow: hidden; text-overflow: ellipsis; min-width: 0; }
.preroll-actions { display: flex; gap: 0.25rem; flex-shrink: 0; }

/* Preroll list row title clamp */
.preroll-row-title { white-space: nowrap; overflow: hidden; text-overflow: ellipsis; }

/* Inline create button row for CategoryPicker dropdown */
.nx-catpicker-create {
  padding: 0.5rem;
  border-bottom: 1px solid var(--border-color);
  background: var(--card-bg);
}

.nx-catpicker-create .button {
  font-size: 0.85rem;
  padding: 0.35rem 0.6rem;
}

/* ---------- Map Root Loading Indicator (theme-aware) ---------- */
.nx-map-progress {
  padding: 0.5rem 0.75rem;
  background: var(--card-bg);
  border: 1px solid var(--border-color);
  border-radius: 8px;
  box-shadow: 0 2px 4px var(--shadow);
}

.nx-spinner {
  width: 16px;
  height: 16px;
  border: 2px solid var(--border-color);
  border-top-color: var(--button-bg);
  border-right-color: var(--button-bg);
  border-radius: 50%;
  animation: nx-spin 0.7s linear infinite;
}

@keyframes nx-spin {
  to { transform: rotate(360deg); }
}

.nx-progress {
  position: relative;
  height: 6px;
  background: var(--header-bg);
  border: 1px solid var(--border-color);
  border-radius: 9999px;
  overflow: hidden;
}

.nx-progress .bar {
  position: absolute;
  left: -40%;
  width: 40%;
  height: 100%;
  background: var(--button-bg);
  border-radius: inherit;
  animation: nx-indeterminate 1.2s ease-in-out infinite;
}

@keyframes nx-indeterminate {
  0%   { left: -40%; }
  50%  { left: 60%; }
  100% { left: 100%; }
}

/* Rockerswitch toggle */
.nx-rockerswitch {
  position: relative;
  display: inline-block;
  width: 50px;
  height: 24px;
  background-color: var(--border-color);
  border-radius: 24px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.nx-rockerswitch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.nx-rockerswitch-slider {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--border-color);
  border-radius: 24px;
  transition: background-color 0.3s ease;
}

.nx-rockerswitch-slider:before {
  position: absolute;
  content: "";
  height: 18px;
  width: 18px;
  left: 3px;
  bottom: 3px;
  background-color: var(--card-bg);
  border: 1px solid var(--border-color);
  border-radius: 50%;
  transition: transform 0.3s ease;
}

input:checked + .nx-rockerswitch-slider {
  background-color: var(--button-bg);
}

input:checked + .nx-rockerswitch-slider:before {
  transform: translateX(26px);
}

/* Respect users who prefer reduced motion */
@media (prefers-reduced-motion: reduce) {
  .nx-spinner { animation: none; }
  .nx-progress .bar { animation: none; left: 0; width: 100%; }
  .nx-rockerswitch-slider { transition: none; }
  .nx-rockerswitch-slider:before { transition: none; }
}

/* Modern Enhanced Prerolls UI (2025-10-01 v2) â€” softer, tonal, theme-consistent */
.prerolls-control-bar {
  --ctl-h: 44px;
  --ctl-radius: 12px;
  display: flex;
  gap: 1rem 1.25rem;
  margin-bottom: 1rem;
  padding: 0.75rem 1rem;
  background: var(--header-bg);
  border-radius: 14px;
  border: 1px solid var(--border-color);
  box-shadow: 0 1px 3px var(--shadow);
  flex-wrap: wrap;
  align-items: center;
}

.control-group {
  display: flex;
  flex-direction: column;
  gap: 0.45rem;
  min-width: 150px;
}

.control-label {
  font-size: 0.76rem;
  font-weight: 600;
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 0.48px;
}

.view-toggle {
  display: inline-flex;
  align-items: center;
  gap: 4px;
  padding: 4px;
  border-radius: 9999px;
  background: var(--card-bg);
  border: 1px solid var(--border-color);
}

.view-btn {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  height: var(--ctl-h);
  padding: 0 0.9rem;
  background: var(--header-bg);
  border: 1px solid var(--border-color);
  border-radius: 9999px;
  cursor: pointer;
  font-size: 0.85rem;
  color: var(--text-color);
  transition: background-color 0.2s ease, color 0.2s ease, transform 0.08s ease, box-shadow 0.2s ease, border-color 0.2s ease;
}

.view-btn:hover {
  background: var(--card-bg);
  box-shadow: 0 1px 3px var(--shadow);
  transform: translateY(-1px);
}

.view-btn.active {
  background: var(--button-bg);
  color: #fff;
  border-color: transparent;
  box-shadow: 0 6px 12px rgba(0,0,0,0.12);
}

.view-btn:active {
  transform: translateY(0);
  box-shadow: 0 3px 8px rgba(0,0,0,0.12);
}

.view-btn:focus-visible {
  outline: none;
  box-shadow: 0 0 0 3px rgba(59,130,246,0.25);
}

.view-icon { font-size: 1rem; }

/* Inputs: dropdowns and text fields with custom arrow and focus ring */
.filter-controls {
  display: flex;
  gap: 0.6rem;
  align-items: center;
}

.filter-select,
.page-size-select,
.bulk-category-select {
  height: var(--ctl-h);
  padding: 0 2rem 0 0.75rem;
  border: 1px solid var(--border-color);
  border-radius: var(--ctl-radius);
  background-color: var(--header-bg);
  color: var(--text-color);
  font-size: 0.9rem;
  min-width: 150px;
  transition: border-color 0.2s ease, box-shadow 0.2s ease, transform 0.08s ease, background-color 0.2s ease;
  box-shadow: inset 0 1px 2px var(--shadow);
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  background-image: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' width='16' height='16' fill='%23666' viewBox='0 0 16 16'><path d='M4 6l4 4 4-4' stroke='%23666' stroke-width='1.5' fill='none' stroke-linecap='round' stroke-linejoin='round'/></svg>");
  background-repeat: no-repeat;
  background-position: right 0.65rem center;
  background-size: 16px 16px;
}

.filter-select:hover,
.page-size-select:hover,
.bulk-category-select:hover {
  border-color: var(--button-bg);
}

.filter-select:focus,
.page-size-select:focus,
.bulk-category-select:focus {
  outline: none;
  border-color: var(--button-bg);
  box-shadow: 0 0 0 3px rgba(59,130,246,0.2);
}

.search-input-wrapper {
  position: relative;
  display: flex;
  align-items: center;
}

.search-input {
  height: var(--ctl-h);
  padding: 0 2rem 0 0.75rem;
  border: 1px solid var(--border-color);
  border-radius: var(--ctl-radius);
  background: var(--header-bg);
  color: var(--text-color);
  font-size: 0.9rem;
  width: 220px;
  transition: border-color 0.2s ease, box-shadow 0.2s ease, background-color 0.2s ease;
  box-shadow: inset 0 1px 2px var(--shadow);
}

.search-input::placeholder {
  color: var(--text-muted);
}

.search-input:focus {
  outline: none;
  border-color: var(--button-bg);
  box-shadow: 0 0 0 3px rgba(59,130,246,0.2);
}

.search-icon {
  position: absolute;
  right: 0.6rem;
  color: var(--text-secondary);
  pointer-events: none;
}

/* Primary action (Apply) */
.filter-btn {
  height: var(--ctl-h);
  padding: 0 1rem;
  background: var(--button-bg);
  color: #fff;
  border: 1px solid transparent;
  border-radius: var(--ctl-radius);
  cursor: pointer;
  font-size: 0.85rem;
  font-weight: 600;
  transition: transform 0.08s ease, filter 0.2s ease, box-shadow 0.2s ease;
  box-shadow: 0 6px 12px rgba(0,0,0,0.12);
}

.filter-btn:hover { filter: brightness(1.05); }
.filter-btn:active { transform: translateY(1px); box-shadow: 0 3px 8px rgba(0,0,0,0.12); }
.filter-btn:focus-visible { outline: none; box-shadow: 0 0 0 3px rgba(59,130,246,0.25); }

/* Bulk bar and actions */
.bulk-actions-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.75rem 1rem;
  background: var(--header-bg);
  border-radius: 14px;
  border: 1px solid var(--border-color);
  margin-bottom: 1rem;
  box-shadow: 0 1px 3px var(--shadow);
  flex-wrap: wrap;
  gap: 1rem;
}

.bulk-actions-left,
.bulk-actions-right {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.select-all-wrapper { display: flex; align-items: center; gap: 0.5rem; cursor: pointer; }
.select-all-checkbox { width: 1rem; height: 1rem; cursor: pointer; }
.select-all-label { font-size: 0.9rem; color: var(--text-color); font-weight: 500; }

.action-btn {
  height: var(--ctl-h);
  padding: 0 0.85rem;
  border: 1px solid var(--border-color);
  border-radius: var(--ctl-radius);
  cursor: pointer;
  font-size: 0.85rem;
  font-weight: 600;
  transition: transform 0.08s ease, filter 0.2s ease, box-shadow 0.2s ease, background-color 0.2s ease, border-color 0.2s ease;
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  background: var(--header-bg);
  color: var(--text-color);
  box-shadow: 0 1px 2px var(--shadow);
}

.action-btn:hover:not(:disabled) { filter: brightness(1.03); box-shadow: 0 2px 4px var(--shadow); }
.action-btn:active:not(:disabled) { transform: translateY(1px); box-shadow: 0 1px 2px var(--shadow); }

.action-btn:disabled { opacity: 0.6; cursor: not-allowed; }

.action-btn.secondary {
  background: var(--header-bg);
  color: var(--text-color);
  border-color: var(--border-color);
}
.action-btn.primary {
  background: #28a745;
  color: #fff;
  border-color: transparent;
}
.action-btn.danger {
  background: #dc3545;
  color: #fff;
  border-color: transparent;
}

/* Dark theme refinements */
body.dark .prerolls-control-bar,
body.dark .bulk-actions-bar {
  background: var(--card-bg);
  border-color: var(--border-color);
  box-shadow: 0 2px 8px var(--shadow);
}

body.dark .view-toggle { background: var(--card-bg); border-color: var(--border-color); }
body.dark .view-btn { background: var(--card-bg); }
body.dark .view-btn:hover { background: var(--header-bg); }

body.dark .filter-select,
body.dark .page-size-select,
body.dark .bulk-category-select,
body.dark .search-input {
  background: var(--card-bg);
  border-color: var(--border-color);
  color: var(--text-color);
  box-shadow: inset 0 1px 2px var(--shadow);
}

/* Dark theme: higher-contrast arrow */
body.dark .filter-select,
body.dark .page-size-select,
body.dark .bulk-category-select {
  background-image: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' width='16' height='16' fill='%23bbb' viewBox='0 0 16 16'><path d='M4 6l4 4 4-4' stroke='%23bbb' stroke-width='1.5' fill='none' stroke-linecap='round' stroke-linejoin='round'/></svg>");
}

/* Better icon contrast on focus */
.search-input-wrapper:focus-within .search-icon { color: var(--button-bg); }

/* Accessibility: reduced motion */
@media (prefers-reduced-motion: reduce) {
  .view-btn, .filter-btn, .action-btn {
    transition: none;
  }
}

/* Responsive adjustments for mobile */
@media (max-width: 768px) {
  .prerolls-control-bar { flex-direction: column; align-items: stretch; gap: 1rem; }
  .filter-controls { flex-direction: column; align-items: stretch; }
  .search-input { width: 100%; }
  .bulk-actions-bar { flex-direction: column; align-items: stretch; gap: 0.75rem; }
  .bulk-actions-left, .bulk-actions-right { justify-content: center; }
  .bulk-category-wrapper { flex-direction: column; align-items: stretch; }
}

/* Shared Toolbar + Checkbox styles (Categories + Schedule Management) */
.nx-toolbar {
  display: flex;
  gap: 0.75rem 1rem;
  align-items: center;
  flex-wrap: wrap;
  padding: 0.75rem 1rem;
  margin-bottom: 1rem;
  background: var(--card-bg);
  border: 1px solid var(--border-color);
  border-radius: 12px;
  box-shadow: 0 2px 10px var(--shadow);
}

.toolbar-group {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
}

.nx-checkrow {
  display: inline-flex;
  align-items: center;
  gap: 1rem;
  flex-wrap: wrap;
}

.nx-check {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.35rem 0.6rem;
  background: var(--header-bg);
  border: 1px solid var(--border-color);
  border-radius: 10px;
  font-size: 0.9rem;
  color: var(--text-color);
  transition: background-color 0.15s ease, border-color 0.15s ease;
}

.nx-check:hover {
  background: var(--card-bg);
}

.nx-check input {
  width: 1rem;
  height: 1rem;
  accent-color: var(--button-bg);
}

/* Dark theme refinements */
body.dark .nx-toolbar {
  background: var(--card-bg);
  border-color: var(--border-color);
  box-shadow: 0 2px 10px var(--shadow);
}

body.dark .nx-check {
  background: var(--card-bg);
  border-color: var(--border-color);
  color: var(--text-color);
}

/* Responsive */
@media (max-width: 768px) {
  .nx-toolbar { flex-direction: column; align-items: stretch; }
  .toolbar-group { width: 100%; }
  .nx-checkrow { gap: 0.75rem; }
}

/* ---------- Icon buttons (micro-visual treatment for small actions in cards/lists) ---------- */
.nx-iconbtn {
  height: 28px;
  min-height: 28px;
  padding: 0 0.5rem;
  border-radius: 9999px;
  border: 1px solid var(--border-color);
  background: linear-gradient(180deg, rgba(255,255,255,0.02), rgba(0,0,0,0.02));
  color: var(--text-color);
  font-size: 0.82rem;
  line-height: 1;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 0.35rem;
  cursor: pointer;
  box-shadow: 0 1px 3px var(--shadow);
  transition: transform 0.08s ease, box-shadow 0.2s ease, filter 0.2s ease, background-color 0.2s ease, border-color 0.2s ease;
}

.nx-iconbtn:hover {
  filter: brightness(1.03);
  transform: translateY(-1px);
  box-shadow: 0 2px 6px var(--shadow);
}

.nx-iconbtn:active {
  transform: translateY(0);
  box-shadow: 0 1px 3px var(--shadow);
}

.nx-iconbtn:focus-visible {
  outline: none;
  box-shadow: 0 0 0 3px rgba(59,130,246,0.25);
}

/* Variants */
.nx-iconbtn--danger {
  background: linear-gradient(180deg, #dc3545, #c82333);
  color: #fff;
  border-color: transparent;
}
.nx-iconbtn--danger:hover { filter: brightness(1.05); }

.nx-iconbtn--muted {
  background: #6c757d;
  color: #fff;
  border-color: transparent;
}

/* Disabled */
.nx-iconbtn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
  filter: none;
  box-shadow: 0 1px 3px var(--shadow);
}

/* Dark mode refinements */
body.dark .nx-iconbtn {
  background: linear-gradient(180deg, rgba(255,255,255,0.04), rgba(0,0,0,0.06));
  border-color: var(--border-color);
  color: var(--text-color);
}

body.dark .nx-iconbtn--danger {
  background: linear-gradient(180deg, #e03131, #c92a2a);
}

body.dark .nx-iconbtn--muted {
  background: #495057;
}

/* Reduced motion */
@media (prefers-reduced-motion: reduce) {
  .nx-iconbtn { transition: none; }
}
